@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  font-family: 'Press Start 2P', cursive;
}

img {
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: pixelated;
}

.App {
  display: flex;
  align-items: center;
  flex-flow: column;
}

.title {
  padding: 2rem 0;
  display: flex;
  flex-flow: row;
  color: #222;
}

.title:hover {
  color: #444;
  text-decoration: none;
}

.title h1 {
  padding: 1rem;
}

.image {
  height: 400px;
  
  -ms-interpolation-mode: nearest-neighbor;
  
      image-rendering: pixelated;
  border: 2px solid black
}

.content {
  width: 85%;
}

footer {
  padding: 2rem;
}

footer a {
  color: #333;
  text-decoration: none;
}

footer a:hover {
  color: #444;
  text-decoration: none;
}
.row {
    padding-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.filter-button {
    text-transform: capitalize;
    margin-bottom: 1rem;
    width: 148px;
}

.subtitle {
    margin-top: 1rem;
}

.subtitle b {
    color: rgb(30, 79, 158);
}

.top Button {
    margin-top: 8px;
    margin-right: 16px;
} 
.link-pokemon {
    color: #222;
    -webkit-transition: 0.1s;
    transition: 0.1s;
}

.link-pokemon:hover {
    color: #444;
    text-decoration: none;
    -webkit-transition: 0.1s;
    transition: 0.1s;
}

.link-pokemon img {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.link-pokemon:hover img {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
#click-area {
    background-color: Transparent;
    border: none;
    overflow: hidden;
    outline:none;
    margin: 0;
}
.pokemon-card {
    margin: 0.5rem;
}

.pokemons-container {
    padding-top: 1rem;
}

.pokemon-img {
    margin: 1rem 2rem;
    -webkit-transform: scale(2);
            transform: scale(2);
}

.poke-container {
    margin: 2rem 0;
}

.evolution-container {
    margin: 1rem 0;   
}
